/*
Color List
#DABFFF
#907AD6
#4F518C
#2C2A4A
#7FDEFF
*/
@media only screen and (min-width:600px){

  .navigation{
    position: fixed;
    bottom: -400px;
    left: 0px;
    display:flex;
    flex-flow: column wrap;
    visibility: hidden;
    transition: bottom 2s;
  }

  .navigation button{
    text-align:left;
    padding: 25px 30px;
    border-top-right-radius:50px;
    border-bottom-right-radius:50px;
    text-shadow: 2px 2px #2C2A4A;
  }

  .navigation.active{
    bottom:100px;
  }
  .navigation button:hover{
    background:#2C2A4A;
  }
  .toggle.active:before{
    box-shadow: 2px 2px#2C2A4A;
  }
}

.navigation.active{
  visibility:visible;
}
.navigation button{
  background-color:rgba(255,0,0,0);
  border:0;
  color:#7FDEFF;
 
}

.toggle{
  position: fixed;
  bottom:20px;
  left:20px;
  width:50px;
  height:50px;
  border-radius:50%;
  border:none;
  background:#000;
  cursor: pointer;
  z-index:1;
}
.toggle:before{
  content:'+';
  position:absolute;
  bottom:0;
  left:0;
  font-size:50px;
  width:100%;
  height:100%;
  color:#7FDEFF;
  text-align:center;
  line-height: 40px !important;
  border-radius:50%;
  background:#000;
  transition:0.5s;
  font-weight:bold;
}


.toggle.active:before{
  transform:rotate(405deg);
  background:#7FDEFF;
  color:#000;
}

@media only screen and (max-width:600px){
  .navigation{
    position: absolute;
    width:100%;
    top:20%;
    bottom:30%;
    
    text-align:center;
    display:flex;
    flex-flow: column wrap;
    visibility: hidden;
  }
  .navigation button{
    padding: 25px 10px;
    text-align:center;
    justify-content: space-between;
    font-size: 1.5rem;
    width:100%;
    text-shadow: 2px 2px #4F518C;
  }

  .overlay{
    position: fixed;
    bottom:20px;
    left:20px;
    width:50px;
    height:50px;
    border-radius:50%;
    background:rgba(0,0,0,.95);
    transition: transform 0.5s;
  }
  .overlay.active{
    transform: scale(100,100);
  }
  
  .navigation button:hover{
    background: #4F518C;
  }

  .toggle.active:before{
    box-shadow: 2px 2px#4F518C;
  }
}